.ListaTareasSeccion {
  display: flex;
  justify-content: space-around;
  cursor: pointer;
  padding: 1rem;
  width: 100%;
  text-align: center;
  transition: all 0.2s ease-in-out;
  height: 1.5rem;
  border-bottom: 2px solid #bfc0c0;
  background-color: #2d3142;
}
.ListaTareasSeccion h1 {
  margin: 0;
  font-size: 1.5rem;
  display: flex;

  align-items: center;
}
.ListaTareasSeccion.activo {
  background-color: #4f5d75;
  border-bottom: 2px solid #ffffff;
}
.ListaTareasSeccion.activo:hover {
  background-color: #ef8354;
  border-bottom: 2px solid #2d3142;
}
.ListaTareasSeccion.activo:hover > h1 {
  color: #2d3142;
}
.ListaTareasSeccion:hover {
  background-color: #4f5d75;
  border-bottom: 2px solid #ef8354;
}
.ListaTareasSeccion:hover > h1 {
  color: #ef8354;
}
