.App {
  text-align: center;
  overflow: hidden;
}
.Cargando {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 0.5rem;
}
.lineaCarga {
  height: 100%;
  background: linear-gradient(
    90deg,
    #2d3142,
    #4f5d75,
    #bfc0c0,
    #ffffff,
    #ef8354
  );
  animation: cargando 2s infinite;
}
@keyframes cargando {
  0% {
    transform: translate(-100%);
  }
  100% {
    transform: translate(150%);
  }
}
